/* Dark Mode Fix - OLED Optimized */

/* Force dark mode styles for all essential elements */
html.dark {
  color-scheme: dark !important;
}

/* Core background - Pure black for OLED */
html.dark body {
  background-color: #000000 !important;
  color: #F8FAFC !important;
}

/* Header */
html.dark .app-header,
html.dark header,
html.dark .fixed.top-0,
html.dark .sticky.top-0,
html.dark .bg-theme-background-card.dark\:bg-theme-dark-background-card,
html.dark .bg-gray-100.dark\:bg-\[\#0B0F19\] {
  background-color: #000000 !important;
  border-color: rgba(31, 41, 55, 0.5) !important;
}

/* Cards and content areas */
html.dark .card,
html.dark .stat-card,
html.dark .card-stat,
html.dark .bg-theme-background-card,
html.dark .bg-white,
html.dark .bg-gray-50,
html.dark .bg-gray-100,
html.dark .dark\:bg-\[\#1B2131\],
html.dark .dark\:bg-\[\#252B3B\],
html.dark .dark\:bg-\[\#323B4B\],
html.dark .modal-content,
html.dark .dropdown,
html.dark .sort-dropdown,
html.dark .view-dropdown,
html.dark .collection-dropdown {
  background-color: #0A0E17 !important;
  border-color: rgba(31, 41, 55, 0.5) !important;
}

/* Input backgrounds */
html.dark input,
html.dark textarea,
html.dark select,
html.dark .input,
html.dark .search-input,
html.dark .sort-button,
html.dark .view-button {
  background-color: #0F1623 !important;
  border-color: rgba(31, 41, 55, 0.5) !important;
  color: #F8FAFC !important;
}

/* Fix color variables */
html.dark {
  --color-bg: #000000 !important;
  --color-bg-card: #0A0E17 !important;
  --color-bg-input: #0F1623 !important;
  --color-bg-hover: #0F1623 !important;
  --color-bg-active: #1E293B !important;
  --color-border: #1E293B !important;
  
  --color-text: #F8FAFC !important;
  --color-text-secondary: #CBD5E1 !important;
  --color-text-tertiary: #94A3B8 !important;
  --color-text-disabled: #64748B !important;
  --color-text-inverse: #0F172A !important;
}

/* Text colors */
html.dark .text-gray-900,
html.dark .text-gray-800,
html.dark .text-gray-700,
html.dark .dark\:text-white,
html.dark .dark\:text-gray-100,
html.dark .dark\:text-gray-200,
html.dark .text-theme-text {
  color: #F8FAFC !important;
}

html.dark .text-gray-600,
html.dark .text-gray-500,
html.dark .dark\:text-gray-300,
html.dark .dark\:text-gray-400,
html.dark .text-theme-text-secondary {
  color: #CBD5E1 !important;
}

/* Direct class overrides */
html.dark .bg-theme-dark-background,
html.dark .dark\:bg-theme-dark-background {
  background-color: #000000 !important;
}

html.dark .bg-theme-dark-background-card,
html.dark .dark\:bg-theme-dark-background-card {
  background-color: #0A0E17 !important;
}

html.dark .bg-theme-dark-background-input,
html.dark .dark\:bg-theme-dark-background-input,
html.dark .bg-theme-dark-background-hover,
html.dark .dark\:bg-theme-dark-background-hover {
  background-color: #0F1623 !important;
}

html.dark .text-theme-dark-text,
html.dark .dark\:text-theme-dark-text {
  color: #F8FAFC !important;
}

html.dark .text-theme-dark-text-secondary,
html.dark .dark\:text-theme-dark-text-secondary {
  color: #CBD5E1 !important;
}

/* Hover states */
html.dark .hover\:bg-gray-100:hover,
html.dark .hover\:bg-gray-200:hover,
html.dark .hover\:bg-gray-50:hover,
html.dark .dark\:hover\:bg-gray-700:hover,
html.dark .dark\:hover\:bg-\[\#252B3B\]:hover,
html.dark .dark\:hover\:bg-\[\#323B4B\]:hover {
  background-color: #1E293B !important;
}

/* Buttons specific styling */
html.dark .btn-primary,
html.dark .btn-secondary,
html.dark .btn-tertiary,
html.dark .btn-danger,
html.dark .btn-default {
  border-color: rgba(31, 41, 55, 0.5) !important;
}

/* Ensure modal and card detail backgrounds are dark */
html.dark .fixed.inset-0 {
  /* Keep this intentionally empty to avoid conflicts with modal backdrops */
}

