/* Make sure dark mode transitions smoothly */
html, body, *, *::before, *::after {
  transition-property: background-color, border-color, color, fill, stroke;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
}

/* Make sure dark mode media query is also handled */
@media (prefers-color-scheme: dark) {
  html.dark {
    background-color: #000000 !important;
  }
  
  html.dark body {
    background-color: #000000 !important;
    color: #F8FAFC !important;
  }
}

/* Fix any remaining issues with hard-coded background colors */
html.dark [class*="dark:bg-[#"],
html.dark [class*="dark\:bg-\[#"] {
  background-color: #0A0E17 !important;
}

/* Ensure button text is visible */
html.dark button,
html.dark .btn {
  color: inherit !important;
}

/* Ensure primary buttons maintain their styling */
html.dark .btn-primary {
  color: #A18BFF !important;
}

/* Fix modals */
html.dark .modal-overlay,
html.dark .card-details-overlay,
html.dark .bg-black\/50 {
  background-color: rgba(0, 0, 0, 0.75) !important;
}

/* Fix content positioning - ensure nothing is hidden under the header */
.pt-20 {
  padding-top: 5rem !important; /* 80px instead of 64px */
}

html.dark .dashboard-page {
  padding-top: 1rem;
}

/* Add more spacing to main content container */
.min-h-screen.dashboard-page {
  padding-top: 0 !important; /* Adjust to remove the excessive top padding */
}

/* Fix the gap between header and summary cards */
.space-y-4.mt-4 {
  margin-top: 1rem !important;
}

.space-y-4.mt-1 {
  margin-top: 1rem !important;
}

/* Fix mobile spacing for CardList */
@media (max-width: 640px) {
  .space-y-4.mt-4, 
  .space-y-4.mt-1 {
    margin-top: 0.75rem !important;
  }
}

/* Mobile-specific fixes for bottom padding and iOS issues */
@media screen and (max-width: 768px) {
  /* Add bottom spacing for Safari iOS to account for home indicator */
  .dashboard-page {
    padding-bottom: env(safe-area-inset-bottom, 20px);
  }
  
  /* Ensure bottom navigation doesn't overlap with content on iOS */
  .bottom-nav {
    padding-bottom: env(safe-area-inset-bottom, 12px);
  }
  
  /* Fix for iOS safe area in card details */
  .card-details-content {
    padding-bottom: env(safe-area-inset-bottom, 20px);
  }
}

/* Force modals to be visible and maintain proper stacking */
.card-details {
  z-index: 100 !important;
}

.settings-modal {
  z-index: 50 !important; /* Lower than bottom nav z-index */
}

.settings-modal-content {
  z-index: 52 !important; /* Higher than bottom nav z-index */
}

/* Fix for modal overlay backgrounds */
.fixed.inset-0.bg-black\/50 {
  background-color: rgba(0, 0, 0, 0.75) !important;
}

/* Fix for modals that might have inline background colors */
[class*="bg-white"] {
  background-color: var(--color-bg-card) !important;
}

html.dark [class*="bg-white"] {
  background-color: #0A0E17 !important;
}

/* Fix bottom nav appearance in dark mode */
html.dark .bottom-nav {
  background-color: #000000 !important;
  border-color: rgba(31, 41, 55, 0.5) !important;
}

/* Fix for card details missing dark mode */
html.dark .card-details-modal,
html.dark .settings-modal,
html.dark .modal-content {
  background-color: #0A0E17 !important;
  color: #F8FAFC !important;
  border-color: rgba(31, 41, 55, 0.5) !important;
}

/* Fix for dropdown item hover states in dark mode */
html.dark .dropdown-item:hover {
  background-color: #1E293B !important;
}

/* Ensure all SVG icons are visible in dark mode */
html.dark svg {
  fill: currentColor !important;
}

/* Ensure inputs have dark backgrounds */
html.dark input,
html.dark textarea,
html.dark select {
  background-color: #121927 !important;
  color: #F8FAFC !important;
  border-color: rgba(31, 41, 55, 0.5) !important;
}

/* Toggle Switch Styles */
.toggle-checkbox {
  right: 0;
  z-index: 5;
  border-color: #60a5fa;
  transition: all 0.3s;
}

.toggle-checkbox:checked {
  right: 4px;
  border-color: #60a5fa;
}

.toggle-checkbox:checked + .toggle-label {
  background-color: #60a5fa;
}

.toggle-label {
  transition: background-color 0.3s;
}

/* Modal backdrop - completely revise this to avoid conflicts */
.fixed.inset-0[style*="backgroundColor"] {
  /* Don't override anything with inline styles */
}

/* Remove all hardcoded background colors for modals */
html.dark .fixed.inset-y-0.right-0,
html.dark .fixed.inset-0 > div[class*="right-0"],
html.dark [class*="settings-modal"],
html.dark [class*="card-details"] {
  /* These were causing conflicts - removing */
}

/* Override any fixed/sticky headers in dark mode */
html.dark .sticky.top-0,
html.dark .fixed.top-0,
html.dark .fixed.inset-y-0.right-0 > div:first-child {
