/* Fix curved underlines in tab navigation */
button[class*="border-b-2"],
.border-b-2,
.border-primary,
[class*="border-b-2 border-primary"] {
  border-radius: 0 !important;
  transform: translateZ(0);
  -webkit-mask-image: -webkit-radial-gradient(white, black); /* Safari fix */
}

/* Set up CSS variables for theme colors */
:root {
  --bg-primary: #FFFFFF;
  --bg-secondary: #F9FAFB;
  --bg-card: #FFFFFF;
  --bg-input: #FFFFFF;
  --text-primary: #111827;
  --text-secondary: #4B5563;
  --text-muted: #9CA3AF;
  --border-color: #E5E7EB;
  
  --transition-theme: background-color 0.15s ease, color 0.15s ease, border-color 0.15s ease;
}

/* Dark mode color variables - will be applied with .dark class */
.dark, :root[class~="dark"] {
  --bg-primary: #000000;
  --bg-secondary: #0A0E17;
  --bg-card: #0A0E17;
  --bg-input: #0A0E17;
  --text-primary: #FFFFFF;
  --text-secondary: #E5E7EB;
  --text-muted: #9CA3AF;
  --border-color: rgba(75, 85, 99, 0.5);
}

/* Apply transitions to specific elements only (avoid transitioning everything) */
.theme-transition {
  transition: var(--transition-theme);
}

/* Apply the transitions to text elements */
h1, h2, h3, h4, h5, h6, p, span, a, button, input, textarea, select {
  transition: var(--transition-theme);
}

/* Avoid transitioning backgrounds of large containers */
body, main, section, article, aside, nav, header, footer {
  transition: none;
}

/* For modals and cards with immediate visual impact */
.modal-content, .card, .panel {
  transition: var(--transition-theme);
} 